<template>
  <div>
        <div v-if="questionPage<16">

      <div v-for="question in questionList" :key="question">
        <div v-if="questionPage===question.id+1">
          <h2>{{ questionPage }}/15</h2>
          <strong style="font-size: 120%;">{{ question.q }}</strong>
          <br><br>
      <div class="qbox" @click="$emit('questionAdd',1); nextQuestion()">{{ question.a }}</div>
      <div class="qbox" @click="$emit('questionAdd',0); nextQuestion()">{{ question.b }}</div>
        </div>
      </div>
    </div>
    <button class="main-button" v-if="questionPage==16" @click="$emit('changePage',2)">테스트 결과 보러가기</button>

  </div>
</template>

<script>
export default {
    name: 'TestComponent',
    props:{
    page:Number,
    question:Array,
    questionList:Object,
  },
  data(){
    return{
      questionPage:1,
    }
  },
  methods:{
    nextQuestion(){
      if(this.questionPage<16){
        this.questionPage+=1;
      }
    },
  },
}
</script>

<style>
.qbox{
  box-sizing: border-box;
  font-family: "consolas";
    /* vertical-align: middle; */
    padding: 10px;
  /* 글씨 가운데 정렬위해 패딩 */
  font-size:100%;
  /* 100%가 나은거같다. */
  width: 100%;
  height: 50px;
  max-width: 350px;
  margin: auto;
  margin-bottom: 20px;
  position: relative;
  border: 2px solid #ffcde8;
  border-radius: 10px;
  cursor: pointer;
}
/* .qbox:hover{
  border: 2px solid #ffe7f4;

} */
</style>